export default {
	data() {
		return {
			nickName: '',
			phone:''
			//u_recommend: {u_recommend}
		}
	},
	
	onLoad:function(options) {
		this.nickName=localStorage.getItem("name");
		this.phone = localStorage.getItem("phone");
		let that = this;
		// uni.login({
		// 	provider: 'weixin',
		// 	success: function(loginRes) {
		// 		// 获取用户信息				
		// 		uni.getUserInfo({
		// 			provider: 'weixin',
		// 			success: function(infoRes) {
		// 				that.account = infoRes.userInfo;
		// 				//console.log(that.account);
		// 			}
		// 		});
		// 	}
		// })
		uni.request({
			url: '/dpc/getUserInfo/getUser?u_phone='+localStorage.getItem("phone"),
			method: 'POST',
			success: res => {
				console.log("数据:"+res.data.uId);
				localStorage.setItem("id",res.data.uId)
			}
		});
		//判断用户是否存在
		uni.request({
			url: 'http://localhost:8081/userRepetition?u_phone='+localStorage.getItem("phone"),
			method: 'POST',
			success: res => {
				if(res.data==0){
					 //成为新用户 
					uni.request({
						url: 'http://localhost:8081/userInsert?u_name='+localStorage.getItem("name")+'&u_phone='+localStorage.getItem("phone")+'&urecommend='+localStorage.getItem("urecommend"),
						method: 'POST',
						success: res => {
							console.log(res.data);
						}
					});
				}
				
			}
		});
		
	},
	methods: {
		//提现点击事件
		withdrawClick: function(){
			uni.reLaunch({
				url: '/pages/withdraw/withdraw'
			});
		},
		//推广二维码点击事件
		towClick: function(){
			uni.reLaunch({
				url: '/pages/dimension/dimension'
			});
		},
		//绑定结算卡点击事件
		cardClick: function(){
			uni.reLaunch({
				url: '/pages/bank/bank'
			});
		}
	}
}
